<template>
  <div class="pickup-tree">
    <el-input :placeholder="placeholder" v-model="filterText"></el-input>
    <el-tree
      class="filter-tree"
      :data="data"
      :props="props"
      :filter-node-method="filterNode"
      ref="fileList"
      :show-checkbox="showCheckbox"
      node-key="id"
      @check-change="handleCheckChange"
      @node-click="handleNodeClick"
    ></el-tree>
  </div>
</template>

<script>
export default {
  name: "PickupTree",
  model: {
    prop: "value",
    event: "input"
  },
  props: {
    value: String,
    treeOptions: {
      required: true,
      type: Object
    }
  },
  mounted() {},
  computed: {
    data() {
      return (this.treeOptions && this.treeOptions.data) || [];
    },
    props() {
      return (this.treeOptions && this.treeOptions.props) || {};
    },
    placeholder() {
      return (
        (this.treeOptions && this.treeOptions.placeholder) ||
        "输入关键字进行过滤"
      );
    },
    showCheckbox() {
      return (this.treeOptions && this.treeOptions.showCheckbox) || false;
    }
  },
  data() {
    return {
      filterText: ""
    };
  },
  methods: {
    getTree() {
      return this.$refs.fileList;
    },
    filterNode(value, data) {
      if (!value) return true;
      console.log(data[this.props.label]);
      return data[this.props.label].indexOf(value) !== -1;
    },
    handleCheckChange(...args) {
      let vm = this;
      vm.handleCheckChange.count = vm.handleCheckChange.count || 1;
      if (vm.handleCheckChange.timer) {
        clearTimeout(vm.handleCheckChange.timer);
        vm.handleCheckChange.timer = false;
      }
      vm.handleCheckChange.timer = setTimeout(() => {
        vm.$emit("check-change", args);
        console.log("vm.handleCheckChange.count", vm.handleCheckChange.count++);
      }, 500);
    },
    handleNodeClick(data, node, vm) {
      this.$emit("node-click", data, node, vm);
      console.log(data, node, vm);
    }
  },
  watch: {
    filterText(val) {
      this.$refs.fileList.filter(val);
    }
  }
};
</script>

