<template>
  <div class="data-grid">
    <slot>
      <el-table :data="tableData" style="width: 100%" height="250">
        <template v-for="(tableH, index) in tableHeader">
          <el-table-column
            :class="'t-column-'+index"
            :prop="tableH.prop"
            :label="tableH.label"
            :width="tableH.width"
            :key="index"
            v-if="!tableH.type"
          ></el-table-column>

          <!-- 操作 -->
          <el-table-column
            v-else-if="tableH.type===TYPE.BUTTON_UPDATE_DELETE"
            :prop="tableH.prop"
            :label="tableH.label"
            :width="tableH.width"
            :key="index"
          >
            <template slot-scope="scope">
              <div class="oprations">
                <el-button @click="handleButtonUpdate(scope.row,scope)" icon="el-icon-edit"></el-button>
                <el-button @click="handleButtonDelete(scope.row,scope)" icon="el-icon-delete"></el-button>
              </div>
            </template>
          </el-table-column>
          <!-- 操作 -->
          <!-- 操作 -->
          <el-table-column
            v-else-if="tableH.type==='checkbox'"
            :prop="tableH.prop"
            :label="tableH.label"
            :width="tableH.width"
            :key="index"
          >
            <template slot-scope="scope">
              <el-checkbox v-model="scope.row[tableH.prop]" :disabled="tableH.disabled"></el-checkbox>
            </template>
          </el-table-column>
          <!-- 操作 -->
          <el-table-column v-else :prop="tableH.prop" :label="tableH.label" :key="index"></el-table-column>
        </template>
      </el-table>
    </slot>
    <div class="pagination-wrapper">
      <div class="pagination">
        <el-pagination
          background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="size"
          layout="total, prev, pager, next"
          :total="total"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
const TYPE = {
  BUTTON_UPDATE_DELETE: "button.update.delete"
};
export default {
  name: "DataGrid",
  model: {
    prop: "tableData",
    event: "input"
  },
  props: {
    tableData: {
      required: true,
      type: Array
    },
    tableHeader: {
      required: true,
      type: Array
    },
    /* pagination */
    info: {
      required: true,
      type: Object
    }
    /* pagination */
  },
  mounted() {},
  computed: {
    total() {
      return (this.info && this.info.total) || 0;
    },
    size() {
      return (this.info && this.info.size) || 10;
    },
    page() {
      return (this.info && this.info.page) || 1;
    }
  },
  data() {
    return {
      TYPE,
      /* pagination */
      currentPage: this.page
      /* pagination */
    };
  },
  methods: {
    filterNode(value, data) {
      if (!value) return true;
      console.log(data[this.props.label]);
      return data[this.props.label].indexOf(value) !== -1;
    },
    handleCheckChange(...args) {
      this.$emit("check-change", args);
      console.log(args);
    },
    handleButtonDelete(row, scope) {
      this.$emit("clickDelete", row, scope);
    },
    handleButtonUpdate(row, scope) {
      this.$emit("clickUpdate", row, scope);
    },
    /* pagination */
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(currentPage) {
      this.$emit("currentPageChange", currentPage);
      console.log(`当前页: ${currentPage}`);
    }
    /* pagination */
  },
  watch: {
    filterText(val) {
      this.$refs.fileList.filter(val);
    }
  }
};
</script>

